import React, { Component } from 'react';
import { observer } from 'mobx-react';
import { observable, computed } from 'mobx';

import './App.css';

import _ from 'lodash';
import Victor from 'victor'; // Vector processing
import Line from "./Line";  // lines between units
import { Circle } from "react-shapes"; // distance circles

const unitTypes = {
    assault: { name: "Assault", short: "AS", hp: 5000, shield: 3000, travelSpeed: 1.2, targettingTime: 1000, bulletsPerBurst: 2, bursts: 3, reload: 100 },
    shotgun: { name: "Shotgun", short: "SG", hp: 3000, shield: 5000, travelSpeed: 2.0, targettingTime: 1000, bulletsPerBurst: 5, bursts: 1, reload: 100 },
    machineGun: { name: "Machine Gun", short: "MG", hp: 7000, shield: 5000, travelSpeed: 0.75, targettingTime: 1500, bulletsPerBurst: 1, bursts: 30, reload: 30 },
    sniper: { name: "Sniper", short: "SN", hp: 3000, shield: 3000, travelSpeed: 1, targettingTime: 3000, bulletsPerBurst: 1, bursts: 1, reload: 0 },
};


const teams = {
    red: { name: "Red", class: "unit-red", colour: "#FF5555" },
    blue: { name: "Blue", class: "unit-blue", colour: "#8888FF" }
}

const actionTypes = {
    idle: { name: "Idle" },
    attack: { name: "Attack" },
    move: { name: "Move" },
    retreat: { name: "Retreat" }
}

const gameParams = {
    largeCircleRadius: 150,
    mediumCircleRadius: 100,
    smallCircleRadius: 50
}

class Unit {
    @observable unitType;
    @observable x=0;
    @observable y=0;
    @observable rotation=0;
    @observable.ref currentAction;
    @observable hp=90;
    @observable shield=90;
    @observable showDistanceRings=false;

    @computed get targetCoordinates() {
        if(this.currentAction && this.currentAction.actionType == actionTypes.move) {
            return { x: this.currentAction.target_unit.x, y: this.currentAction.target_unit.y }
        }
        else
            return null;
    }

    toggleDistanceRings() {
        this.showDistanceRings = !this.showDistanceRings;
    }
}

class Board {
    @observable units = [];
    @observable width = 900;
    @observable height= 600;

    addUnit(unitConfig) {
        var unit = new Unit();
        unit.x = unitConfig.x;
        unit.y = unitConfig.y;
        unit.team = unitConfig.team;
        unit.unitType = unitConfig.type;
        unit.rotation = 0;
        this.units.push(unit);
    }
}

class Game {
    @observable time = 0;
    @observable board;

    constructor(config) {
        this.defaultTickRate = 50
        this.config = config
        this.initialize()
    }

    initialize() {
        this.time = 0
        this.board = new Board()
        for(var u of this.config.units) {
            this.board.addUnit(u)
        }
    }

    pause() {
        clearInterval(this.ticker);
        this.ticker = null;
    }

    fastMotion() {
        this.pause()
        this.start(10)
    }

    slowMotion() {
        this.pause()
        this.start(100)
    }

    play() {
        this.pause()
        this.start()
    }

    start(tickOverride) {
        if (!this.ticker)
          this.ticker = setInterval(() => this.tick(), tickOverride || this.defaultTickRate);
    }

    tick() {
        this.time += 50;
        for(var unit of this.board.units) {
            this.getNextAction(unit);
            if (unit.currentAction.actionType.name === actionTypes.idle.name) {}
            if (unit.currentAction.actionType.name === actionTypes.attack.name)
                this.shootWith(unit);
            if (unit.currentAction.actionType === actionTypes.move) 
                this.moveWith(unit);
        }
    }

    getNextAction(unit) {
        if (this.time % 2000 == 0) {
            // Change target every 2 seconds
            unit.currentAction = {
                actionType: actionTypes.move,
                target_unit: _.sample(this.board.units.slice())
            }
        } else {
            // Use the previously set action
            unit.currentAction = unit.currentAction || {
                actionType: actionTypes.idle
            }
        }
    }

    moveWith(unit) {
        var { x:sourceX, y:sourceY, currentAction: { target_unit: { x: targetX, y: targetY }}} = unit;
        if (unit === unit.currentAction.target_unit) {
            unit.rotation = 0;
            return;
        }
        var diff = new Victor(targetX - sourceX, targetY - sourceY).normalize();
        unit.x -= diff.x * unit.unitType.travelSpeed;
        unit.y -= diff.y * unit.unitType.travelSpeed;
        var angle = Math.abs(diff.angleDeg()) - 90;
        // if (angle >= 180) angle -= 180;
        unit.rotation = _.round(angle);
    }
}

const Bar = ({ color, percent }) =>
    <div className="bar">
      <div className="level" style={{ backgroundColor: color, width: percent+"%"}}/>
    </div>

@observer
class UnitView extends Component {
    render() {
        const { unit, unit: { x, y, team, shield, hp, rotation, unitType: { short } } } = this.props;
        return (
            <div className={"unit shadow"} style={{ top: y, left: x }} onClick={() => unit.toggleDistanceRings()}>
                <Bar color={"white"} percent={shield}/>
                <Bar color={unit.team.colour} percent={hp}/>
                <div className={team.class} style={{ transform: "rotate("+rotation+"deg)" }} >
                  { short }
                </div>
            </div>
        )
    }
}

@observer
class DistanceCircles extends Component {
    render() {
        const { unit, unit: { x,y } } = this.props
        const { largeCircleRadius, mediumCircleRadius, smallCircleRadius } = gameParams;
        if (!unit.showDistanceRings) return null;
        return (
            <div className="item" style={{ left:x - largeCircleRadius, top: y - largeCircleRadius}}>
                {
                    [largeCircleRadius, mediumCircleRadius, smallCircleRadius].map(r =>
                        <div className="item" style={{ left: largeCircleRadius - r, top: largeCircleRadius - r }}>
                            <Circle
                                r={r}
                                fill={{color:'rgba(200,200,200,0.05)'}}
                                stroke={{color:'rgba(0,0,0,0.1)'}}
                                strokeWidth={1} />
                        </div>
                    )
                }
            </div>
        )
    }
}

@observer
class BoardView extends Component {
    render() {
        const { board: { width, height, units } } = this.props
        return (
            <div className="board" style={{width: width, height: height}}>
                { units.map( (unit,i) => <DistanceCircles key={i} unit={unit}/>) }
                { units.map( (unit,i) => <UnitView key={i} unit={unit}/>) }
                {
                    units.map( (unit,i) => 
                        <Line
                          key={i}
                          opacity={0.5}
                          unit={unit}
                          offset={unit.team == teams.red ? 2 : -2}
                          style={"2px dashed "+unit.team.colour}
                          /> 
                    )
                }
            </div>
        )
    }
}

var config = {
    units: [
        { team: teams.red, type: unitTypes.assault, x: 200, y: 100 },
        { team: teams.red, type: unitTypes.machineGun,x: 200, y: 200 },
        { team: teams.red, type: unitTypes.shotgun, x: 200, y: 300 },
        { team: teams.red, type: unitTypes.assault, x: 200, y: 400 },
        { team: teams.red, type: unitTypes.assault, x: 200, y: 500 },
        { team: teams.blue, type: unitTypes.assault,x: 700, y: 100 },
        { team: teams.blue, type: unitTypes.sniper, x: 700, y: 200 },
        { team: teams.blue, type: unitTypes.assault,x: 700, y: 300 },
        { team: teams.blue, type: unitTypes.assault,x: 700, y: 400 },
        { team: teams.blue, type: unitTypes.sniper,x: 700, y: 500 },
    ]
}

@observer
class App extends Component {

    constructor(props) {
        super(props);
        this.game = new Game(config);
        this.state = {
            game: this.game,
        }
        this.game.start();
    }

    reset() {
        this.game = new Game(config);
        this.setState({
            game: this.game
        })
    }

    render() {
        const { game, game: { time, board }} = this.state;
        return (
            <div className="App">
                <div className="time">Time: { time }ms</div>
                <div className="description" >
                    Change retreat target every 2 seconds. 
                    Click on units to show distances
                </div>
                <div className="controls">
                  <a href="#" onClick={() => game.pause()}>Pause</a> &nbsp;
                  <a href="#" onClick={() => game.play()}>Play</a> &nbsp;
                  <a href="#" onClick={() => game.slowMotion()}>Slow Motion</a> &nbsp;
                  <a href="#" onClick={() => game.fastMotion()}>Speedup</a> &nbsp;
                  <a href="#" onClick={() => this.reset()}>Reset</a>
                </div>
                <BoardView board={board}/>
            </div>
        );
    }
}

export default App;



